import "./ProductSlider.scss";
import React from "react";

import { Link } from 'react-router-dom';
// SLIDER
import Slider from "react-slick";
// cards
import { Card, CardMedia, CardContent, Typography } from '@material-ui/core';

// settings
const settings = {
  dots: false,
  arrows: false,
  infinite: true,
  autoplay: false,
  speed: 500,
  slidesToShow: 4,
  slidesToScroll: 4,
  responsive: [
    {
      breakpoint: 1200,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 3,
      }
    },
    {
      breakpoint: 900,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
      }
    },
    {
      breakpoint: 700,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
        dots: true,
      }
    },
  ]
};
// slider items
const popularItemsArr = [
  {id:'popularItemsCard1', link: './pages/product/card1.png', text: 'Kristina Dam Oak Table With White', text1: 'Marble Top', price: 799.55},
  {id:'popularItemsCard2', link: './pages/product/card2.png', text: 'Kristina Dam Oak Table With White', text1: 'Marble Top', price: 195.00},
  {id:'popularItemsCard3', link: './pages/product/card3.png', text: 'Activate Facial Mask and Charcoal', text1: 'Soap', price: 129.55},
  {id:'popularItemsCard4', link: './pages/product/card4.png', text: 'Cocktail Table Walnut', text1: '| YES', price: 629.99},
  {id:'popularItemsCard5', link: './pages/product/card1.png', text: 'Lorem ipsum dolor', text1: 'Sit amet', price: 100},
  {id:'popularItemsCard6', link: './pages/product/card2.png', text: 'Nostrum, quasi quam', text1: 'Earum tempora', price: 200},
  {id:'popularItemsCard7', link: './pages/product/card3.png', text: 'Blanditiis, possimus repudiandae', text1: 'Et', price: 300},
  {id:'popularItemsCard8', link: './pages/product/card4.png', text: 'inventore unde nostrum', text1: 'Quidem nam', price: 400},
];
function template() {
  return (
    <Slider {...settings}>
      {popularItemsArr.map(item => 
        <div key={item.id}>
          <Link to="/product"><Card style={{ height: 370, width: 270, margin: '30px auto', cursor: 'pointer'}} onClick={() => this.getCurrentProduct(item)}>
            <CardMedia image={item.link} title="card" style={{ height: 270}}/>
            <CardContent style={{ height: 70}}>
              <Typography variant="body2" color="textSecondary" component="p" style={{ textAlign: 'center' }}>
                {item.text}<br/>
                {item.text1}
              </Typography>
              <Typography variant="body2" component="p" style={{ marginTop: 10, color: '#bdc3c7', textAlign: 'center'}}>
                $ {item.price}
              </Typography>
            </CardContent>
          </Card></Link>
        </div>
      )}
    </Slider>
  );
};

export default template;
