import React    from "react";
import template from "./Category.jsx";
import axios from 'axios';
import { Card, CardMedia, CardContent, Typography } from '@material-ui/core';
class Category extends React.Component {
  constructor() {
    super()
    this.state = {
      itemsJSON: {},
      ishidden: false,
      filterBtnText: 'Hide Filter',
    }
  }
  getCurrentProduct(product) {
    product.name = product.text;
    product.name1 = product.text1;
    localStorage.product = JSON.stringify(product);
  }
  toggleFilter = () => {
    this.setState({
      ishidden: !this.state.ishidden
    })
    this.state.filterBtnText === 'Hide Filter' ? this.setState({filterBtnText:'Show Filter'}) : this.setState({filterBtnText:'Hide Filter'});
  }
  getMoreItems() {
    const url = './data/popular-items.json';
    axios.get(url, this.state)
    .then(json => {
      this.setState({ itemsJSON: json.data.items });
    })
  }
  render() {
    // items JSON
    if(this.state.itemsJSON.length) {
      this.jsonItems = this.state.itemsJSON.map(jsonItem => 
        <Card key={jsonItem.id} style={{ height: 370, width: 270, borderRadius: '6px', margin: 10}}>
          <CardMedia image={jsonItem.img} title="logo_small" style={{height: 270, backgroundSize: 'contain'}}/>
            <CardContent style={{ height: 70}}>
              <Typography variant="body2" color="textSecondary" component="p" style={{ marginBottom: '10px'}}>
                {jsonItem.content}
              </Typography>
              <Typography variant="body2" color="primary" component="p" style={{ marginTop: 10}}>
                {jsonItem.price}
              </Typography>
            </CardContent>
        </Card>
      )
    }
    return template.call(this);
  }
}

export default Category;
