import "./Cookie.scss";
import React from "react";
import { Link } from 'react-router-dom';

function template() {
  const {isVisible} = this.state;
  return (
    <div className={`cookie ${isVisible ? 'isVisible' : '' }`} onClick={()=>this.closeCookies()}>
      <div className="cookie__content">
        <h6>This website uses cookies</h6>
        <p>
          OSF uses its own and third-party cookies for statistikal purposes, to know your preferences, for website performance <br/>
          and interaction with social media offering publicity tailored to your interests. If you continue browsing, we consider that you accept its use.<br/>
          You can expand this information consulting out <span><Link to="/">Cookies Policy Page</Link></span> 
        </p>
        <button className="cookie__accept-btn" onClick={()=>this.acceptCookies()}>Accept</button>
        <button className="cookie__close-btn" onClick={()=>this.closeCookies()}><img src="./components/cookie/close.png" alt=""/></button>
      </div>
    </div>
  );
};
export default template;
