import "./Login.scss";
import React from "react";
import { Link } from 'react-router-dom';
function template() {
  return ( 
    <div className={`login ${this.props.loginIsVisible ? 'loginIsVisible' : '' }`}>
      <div className="login__overlay" onClick={() => this.props.toggleLogin()}></div>
        <div className="login__content">
          <button className="login__close-btn" onClick={() => this.props.toggleLogin()}><img src="./components/login/close.png" alt=""/></button>
          <h6>Sign in</h6>
          <form onSubmit={this.LoginValidation}>
            <label>Email</label>
            <input id="logEmail" type="text" className="login__email" placeholder="osf@mail.com" onFocus={this.changeBorders}/>
            <label>Lato</label> 
            <div className="login__password-wrapper">
              <Link to="/" className="login__forgot-password">forgot password</Link>
              <input id="logPass" type={this.state.passwordType} className="login__password" placeholder="1abcA$" onFocus={this.changeBorders}/>
              <button className="login__show-password-btn" onClick={this.preventD} onMouseDown={this.showPassword} onMouseUp={this.hidePassword}>
                <img src="./components/login/eye.png"/>
              </button>
            </div>      
            <button className="login__accept-btn">LOGIN</button>
          </form>
          <Link to="/">I don't have an account</Link>
        </div>
    </div>
  );
};

export default template;

