import "./ProductSelect.scss";
import React from "react";
import Select from 'react-styled-select'

const ColorSelect = () => {
  const options = [
    { label: "Gray", value: "gray"},
    { label: "Blue", value: "blue"},
    { label: "Pink", value: "pink"},
    { label: "Azure", value: "azure"},
    { label: "Orange", value: "orange"},
  ]
  return (
    <Select className="color-select" options={options} 
      placeholder="Dark Gray"
      searchable={false}
      classes={{
        selectValue: 'color-select-value',
        selectArrow: 'color-select-arrow',
        selectArrowZone: 'color-select-arrowZone',
        selectControl: 'color-select-control',
        selectOption: 'color-select-option',
        selectPlaceholder: 'color-select-placeholder',
        selectMultiValueWrapper: 'color-select-MultiValueWrapper',
        selectMenu: 'color-select-menu',
        selectMenuOuter: 'color-select-MenuOuter',
      }}
    />
  )
}

function template() {
  return (
    <div className="color-select-wr">
        <ColorSelect />
    </div>
  );
};

export default template;
