import React    from "react";
import template from "./Login.jsx";

class Login extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      passwordType: 'password'
    }
  }

  LoginValidation = e => {
    e.preventDefault();
    const email = e.target.logEmail;
    const checkEmail = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    const password = e.target.logPass;
    const checkPassword = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{4,8}$/;
    if(checkEmail.test(email.value) && password.value.match(checkPassword)) {
      alert('Email and password are correct!');
      email.value = '';
      password.value = '';
      e.target.logEmail.style.border = '2px solid green';
      e.target.logPass.style.border = '2px solid green';
    } else {
      // email 
      if(!checkEmail.test(email.value)) { 
        alert('Email wrong!')
        e.target.logEmail.style.border = '2px solid red';
      }
      // password
      if(!password.value.match(checkPassword)) { 
        alert('Password wrong! \n-at least 6 characters length \n-contain 1 numeric \n-1 uppercase letter \n-1 special character');
        e.target.logPass.style.border = '2px solid red';
      }
    }
  }
  changeBorders = e => {
    e.target.style.border = '2px solid #d7d7d7';
  }
  showPassword = () => {
    this.setState({passwordType: ''});
  }
  hidePassword = () => {
    this.setState({passwordType: 'password'});
  }
  preventD = e => {
    e.preventDefault();
  }
  render() {
    return template.call(this);
  }
}
export default Login;