import React    from "react";
import template from "./Cart.jsx";
// redux 
import { connect } from 'react-redux';

class Cart extends React.Component {
  constructor() {
    super();
    this.state = {
      shippingVal: 0,
      cartList: [],
      subTotalPrice: 0,
      totalPrice: 0,
    }
  }
  // first card
  countQuantity = (e, id) => {
    let value = +e.target.value.replace(/\D/g, '');
    value = (value < 1 || value > 100) ? 1 : value;
    const cards = JSON.parse(localStorage.cartList);
    const [thisCard] = cards.filter(item => item.id === id);
    thisCard.quantity = value;
    this.setState({cartList: cards});
    localStorage.cartList = JSON.stringify(cards);
    this.countSubtotalPrice();
    this.props.dispatch({ type: "GET_CART_LIST_QUANTITY" });
  }
  increaseQuantity = (id) => {
    const cards = JSON.parse(localStorage.cartList);
    const [thisCard] = cards.filter(item => item.id === id);
    if(thisCard.quantity > 99)  {
      thisCard.quantity = 100
    } else {
      thisCard.quantity = thisCard.quantity + 1
    }
    this.setState({cartList: cards});
    localStorage.cartList = JSON.stringify(cards);
    this.countSubtotalPrice();
    this.props.dispatch({ type: "GET_CART_LIST_QUANTITY" });
  }
  decreaseQuantity = (id) => {
    const cards = JSON.parse(localStorage.cartList);
    const [thisCard] = cards.filter(item => item.id === id);
    if(thisCard.quantity < 2)  {
      thisCard.quantity = 1
    } else {
      thisCard.quantity = thisCard.quantity - 1
    }
    this.countSubtotalPrice();
    this.setState({cartList: cards});
    localStorage.cartList = JSON.stringify(cards);
    this.props.dispatch({ type: "GET_CART_LIST_QUANTITY" });
  }
  removeItem = (id) => {
    const restCards = JSON.parse(localStorage.cartList).filter(item => item.id !== id);
    this.setState({cartList: restCards});
    localStorage.cartList = JSON.stringify(restCards);
    this.countSubtotalPrice();
    this.props.dispatch({ type: "GET_CART_LIST_QUANTITY" });
  }
  getRadioValue(e) {
    if(e.target.type === 'radio') {
      const shippingPrice = this.state.subTotalPrice + (+e.target.value);
      this.setState({totalPrice: shippingPrice});
    }
  }
  componentDidMount() {
    if(localStorage.cartList) {
      const cartListLS = JSON.parse(localStorage.cartList);
      this.setState({cartList: cartListLS});
      this.countSubtotalPrice();
    }
  }
  countSubtotalPrice = () => {
    const result = JSON.parse(localStorage.cartList).reduce((sum, current) => {
      return sum + current.price * current.quantity;
    }, 0);
    this.setState({subTotalPrice: result});
  }
  render() {
    if(this.state.cartList.length) {
      this.cartList = this.state.cartList.map(item => 
        <div className="cart__item-wr" key={item.id}>
          {/* left bar */}
          <div className="cart__item-left-bar">
            <img src={item.link} alt=""/>
            <div className="cart__item-product-text">
              <h4>{item.name}</h4>
              <p>$ {item.price}</p>
            </div>
          </div>
          {/* right bar */}
          <div className="cart__item-right-bar">
            <div className="cart__product-quantity">
              <button className="cart__minus-btn" onClick={() => this.decreaseQuantity(item.id)}>-</button>
              <input type="text" maxLength="3" value={item.quantity} onChange={(e) => this.countQuantity(e, item.id)}/>
              <button className="cart__plus-btn" onClick={() => this.increaseQuantity(item.id)}>+</button>
            </div>
            <div className="cart__product-price">$ {(item.price*item.quantity).toFixed(2)}</div>
            <button variant="outlined" className="cart__del-btn" onClick={() => this.removeItem(item.id)}>x</button>
          </div>
        </div>
      )
    } else {
      this.cartList = <p className="cart__empty-list">Cart list is empty</p>
    }
    return template.call(this);
  }
}

export default connect()(Cart);