const RadioButtons = () => {
  const [selectedValue, setSelectedValue] = React.useState();
  function handleChange(event) {
    setSelectedValue(event.target.value);
  }
  return (
    <div className="radios">
      <Radio value="gray" style = {{ padding:'0', color:"rgb(69, 65, 62)" }} checked={selectedValue === 'gray'} onChange={handleChange} />
      <Radio value="azure" style = {{ padding:'0', color:"rgb(67, 192, 207)" }} checked={selectedValue === 'azure'} onChange={handleChange} />
      <Radio value="darkGreen" style = {{ padding:'0', color:"rgb(61, 64, 47)" }} checked={selectedValue === 'darkGreen'} onChange={handleChange} />
      <Radio value="green" style = {{ padding:'0', color:"rgb(74, 175, 0)" }} checked={selectedValue === 'green'} onChange={handleChange} />
      <Radio value="blue" style = {{ padding:'0', color:"rgb(49, 91, 189)" }} checked={selectedValue === 'blue'} onChange={handleChange} />
      <Radio value="orange" style = {{ padding:'0', color:"rgb(236, 97, 21)" }} checked={selectedValue === 'orange'} onChange={handleChange} />
      <Radio value="red" style = {{ padding:'0', color:"rgb(220, 4, 29)" }} checked={selectedValue === 'red'} onChange={handleChange} />
      <Radio value="pink" style = {{ padding:'0', color:"rgb(239, 234, 215)" }} checked={selectedValue === 'pink'} onChange={handleChange} />
    </div>
  );
}

