import React    from "react";
import template from "./ProductImageGallery.jsx";
// redux 
import { connect } from 'react-redux';

class ProductImageGallery extends React.Component {
  constructor() {
    super();
    this.state = {
      showMore: false,
      productQuantity: localStorage.productQuantity || 1,
      btnText: 'Read more',
      // slider
      showIndex: false,
      showBullets: true,
      infinite: true,
      showThumbnails: true,
      showFullscreenButton: true,
      showGalleryFullscreenButton: true,
      showPlayButton: false,
      showGalleryPlayButton: true,
      showNav: false,
      isRTL: false,
      slideDuration: 450,
      slideInterval: 2000,
      slideOnThumbnailOver: true,
      thumbnailPosition: 'right',
      showVideo: {},
    }
  }
  toggleReadMore() {
    this.setState({
      showMore: !this.state.showMore
    })
    this.state.btnText === 'Read more' ? this.setState({btnText: 'Show less'}) : this.setState({btnText: 'Read more'});
  }
  countQuantity = e => {
    let value = e.target.value.replace(/\D/g, '');
    value = (value < 1 || value > 109) ? 1 : value;
    this.setState({productQuantity: value});
    localStorage.productQuantity = value;
  }
  increaseQuantity = () => {
    if(this.state.productQuantity > 99)  {
      localStorage.productQuantity = 100
      this.setState({productQuantity: 100})
    } else {
      localStorage.productQuantity = +this.state.productQuantity + 1
      this.setState({productQuantity: +this.state.productQuantity + 1})
    }
  }
  decreaseQuantity = () => {
    if(this.state.productQuantity < 2)  {
      localStorage.productQuantity = 1
      this.setState({productQuantity: 1})
    } else {
      localStorage.productQuantity = +this.state.productQuantity - 1
      this.setState({productQuantity: +this.state.productQuantity - 1})
    }
  }
  addToCartList() {
    const product = JSON.parse(localStorage.product);
    product.quantity = 1;
    if(localStorage.productQuantity) {
      var productQuantity = JSON.parse(localStorage.productQuantity);
    } else {
      productQuantity = 1;
    }
    if(localStorage.cartList) {
      var cartList = JSON.parse(localStorage.cartList);
    } else {
      cartList = [];
    }
    if(cartList.length) {
        const cardInLocalStorage = cartList.find(item => item.id === product.id);
        if(cardInLocalStorage) {
          cardInLocalStorage.quantity = productQuantity;
          localStorage.cartList = JSON.stringify(cartList); 
        } else {
          product.quantity = productQuantity;
          cartList.push(product);
          localStorage.cartList = JSON.stringify(cartList); 
        }
    } else {
      cartList.push(product);
      localStorage.cartList = JSON.stringify(cartList); 
    }
    this.props.dispatch({ type: "GET_CART_LIST_QUANTITY" });
  }
  componentDidMount() {
    this.props.dispatch({ type: "GET_ITEM" });
  } 
  render() {
    return template.call(this);
  }
}
const mapStateToProps = (state) => ({
  item: state.item,
});
export default connect(mapStateToProps)(ProductImageGallery);