import React    from "react";
import template from "./Cookie.jsx";

class Cookie extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isVisible: !localStorage.isVisible
    };
  }
  componentDidMount() {
    const currentComponent = this;
    if(localStorage.isVisible === 'false') {
      currentComponent.setState({ isVisible: false })
    }
    if(!localStorage.isVisible) {
      setTimeout(function() { 
        currentComponent.setState({ isVisible: true })
      }, 1000);
    }
  }
  acceptCookies() {
    this.setState({ isVisible: false })
    localStorage.isVisible = false;
  }
  closeCookies() {
    this.setState({
      isVisible: !this.state.isVisible
    })
  }
  render() {
    return template.call(this);
  }
}

export default Cookie;
