import React    from "react";
import template from "./NavHeader.jsx";
// redux 
import { connect } from 'react-redux';

class NavHeader extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isActive: false,
      loginIsVisible: false,
    }
  }
  componentDidMount() {
    this.props.dispatch({ type: "GET_WISH_LIST_QUANTITY" });
    this.props.dispatch({ type: "GET_CART_LIST_QUANTITY" });
  } 
  resetWishQuantity = () => {
    localStorage.wishQuantity = 0;
    this.props.dispatch({ type: "GET_WISH_LIST_QUANTITY" });
  }
  toggleLogin = () => this.setState(state => ({loginIsVisible: !state.loginIsVisible}));
  toggleActive() {
    this.setState({
      isActive: !this.state.isActive
    })
  }
  render() {
    return template.call(this);
  }
}
const mapStateToProps = (state) => ({
  wishQuantity: state.wishQuantity,
  cartListQuantity: state.cartListQuantity,
  isPrintPage: state.isPrintPage
});
export default connect(mapStateToProps)(NavHeader);
