import React from 'react';
import './App.scss';
// pages
import Home from './pages/Home'
import Release from './pages/Release'
import Category from './pages/Category'
import Cart from './pages/Cart'
import Product from './pages/Product'
import Print from './pages/Print'
import NonProduct from './pages/NonProduct'

// components
import NavHeader from './components/NavHeader'
import NavFooter from './components/NavFooter'
import Cookie from './components/Cookie'
// import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';

// redux
import { createStore } from 'redux';
import { Provider } from 'react-redux';

const initialState = { 
  wishQuantity: 0,
  cartListQuantity: 0,
  isPrintPage: false,
  item: {},
};

function reducer(state = initialState, action) {
  switch(action.type) {
    case "GET_WISH_LIST_QUANTITY": {
      if(localStorage.wishQuantity) {
        return Object.assign({}, state, {
          wishQuantity: JSON.parse(localStorage.wishQuantity)
        })
      } else {
        return Object.assign({}, state, {
          wishQuantity: 0
        })
      }
      break;
    }
    case "GET_CART_LIST_QUANTITY": {
      if(localStorage.cartList) {
        const result = JSON.parse(localStorage.cartList).reduce((sum, current) => {
          return sum + current.quantity;
        }, 0);
        return Object.assign({}, state, {
          cartListQuantity: result
        })
      } else {
        return Object.assign({}, state, {
          cartListQuantity: 0
        })
      }
      break;
    }
    case "PRINT_PAGE": {
      return Object.assign({}, state, {
        isPrintPage: true
      })
      break;
    }
    case "HOME_PAGE": {
      return Object.assign({}, state, {
        isPrintPage: false
      })
      break;
    }
    case "GET_ITEM": {
      return Object.assign({}, state, {
        item: JSON.parse(localStorage.product)
      })
      break;
    }
    default: return state;
  }
}
const store = createStore(reducer);

function App() {
  return (
    <div className="App">
      <Provider store={store}>
        <Router>  
            <NavHeader />
            <Switch>
              <Route path='/' exact component = { Home } />
              <Route path='/release' component = { Release } />
              <Route path='/cart' component = { Cart } />
              <Route path='/nonProduct' component = { NonProduct } />
              <Route path='/category' component = { Category } />
              <Route path='/product' component = { Product } />
              <Route path='/print' component = { Print } />
            </Switch>
            <NavFooter />
            <Cookie />
        </Router>
      </Provider>
    </div>
  );
}
export default App;

