// Tab settings
function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <Typography component="div" role="tabpanel" hidden={value !== index} id={`tabpanel-${index}`} 
      aria-labelledby={`tab-${index}`} {...other}>
      <Box p={3}>{children}</Box>
    </Typography>
  );
}
