import React    from "react";
import template from "./PopularItems.jsx";
import axios from 'axios';
import { Card, CardMedia, CardContent, Typography, IconButton } from '@material-ui/core';
import { Link } from 'react-router-dom';
// redux 
import { connect } from 'react-redux';

class PopularItems extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      popularItems: {},
    };
  }
  getCurrentProduct(product) {
    product.quantity = 1;
    product.name = product.text;
    product.name1 = product.text1;
    localStorage.product = JSON.stringify(product);
  }
  getMoreItems() {
    const url = './data/popular-items.json';
    axios.get(url, this.state)
    .then(json => {
      this.setState({ popularItems: json.data.items });
    })
  }
  addToCartList(cardParams) {
    cardParams.quantity = 1;
    cardParams.name = cardParams.text;
    cardParams.name1 = cardParams.text1;
    if(localStorage.cartList) {
      var cartList = JSON.parse(localStorage.cartList);
    } else {
      cartList = [];
    }
    if(cartList.length) {
        const cardInLocalStorage = cartList.find(item => item.id === cardParams.id);
        if(cardInLocalStorage) {
          cardInLocalStorage.quantity = +cardInLocalStorage.quantity + 1;
          localStorage.cartList = JSON.stringify(cartList); 
        } else {
          cartList.push(cardParams);
          localStorage.cartList = JSON.stringify(cartList); 
        }
    } else {
      cartList.push(cardParams);
      localStorage.cartList = JSON.stringify(cartList); 
    }
    this.props.dispatch({ type: "GET_CART_LIST_QUANTITY" });
  }
  addToWishes() {
    if(localStorage.wishQuantity) {
      localStorage.wishQuantity = +localStorage.wishQuantity + 1;
    } else {
      localStorage.wishQuantity = 1;
    }
    this.props.dispatch({ type: "GET_WISH_LIST_QUANTITY" });
  }
  render() {
    const popularItemsArr = [
      {id:'popularItemsCard1', link: './components/popular_items/card1.png', text: 'Kristina Dam Oak Table With White', text1: 'Marble Top', price: 799.55, simpleOverlay: true},
      {id:'popularItemsCard2', link: './components/popular_items/card2.png', text: 'Hay - About A Lounge', text1: 'Chair AAL 93', price: 600, buyBtn: true},
      {id:'popularItemsCard3', link: './components/popular_items/card3.png', text: 'Activate Facial Mask and', text1: 'Charcoal Soap', price: 129.55, simpleOverlay: true},
      {id:'popularItemsCard4', link: './components/popular_items/card4.png', text: 'Cocktail Table Walnut', text1: '| YES', price: 299.99, simpleOverlay: true},
      {id:'popularItemsCard5', link: './components/popular_items/card5.png', text: 'Hay - About A Lounge', text1: 'Chair AAL 93', price: 659.55, simpleOverlay: true},
      {id:'popularItemsCard6', link: './components/popular_items/card6.png', text: 'TORY DESK CALENDAR', price: 410.99, simpleOverlay: true, cardsMargin: 15},
      {id:'popularItemsCard7', link: './components/popular_items/card7.png', text: 'CH445 Wing Chair on', text1: 'SUITE NY', price: 330.55, simpleOverlay: true},
      {id:'popularItemsCard8', link: './components/popular_items/card8.png', link2: './components/popular_items/message.png', messageText: 'My dragons are', messageText1: 'misbehaving again.', messageText2: 'Unbelieveable!', messageText3: '5H AGO', message: true},
    ];
    this.popularItemsCards = popularItemsArr.map(item => {
      // buttons add to cart list + add to wisch list
      if(item.simpleOverlay) {
        return (
          <div className="slide" key={item.id}>
            <Card style={{ height: 370, width: 270, margin: '30px 10px'}} className='popular-items__card simple'>
              <div className='popular-items__card-overlay'>
                <IconButton onClick={()=>this.addToCartList(item)}><img src="./components/popular_items/plus_btn.png" alt=""/></IconButton>
                <IconButton onClick={()=>this.addToWishes()}><img src="./components/popular_items/heart_btn.png" alt=""/></IconButton>
              </div>
              <CardMedia image={item.link} title="card" style={{ height: 270}}/>
              <CardContent style={{ height: 70}}>
                <Typography variant="body2" color="textSecondary" component="p" style={{ marginTop: item.cardsMargin ? item.cardsMargin : 0 }}>
                  {item.text}<br/>
                  {item.text1}
                </Typography>
                <Typography variant="body2" color="primary" component="p" style={{ marginTop: item.cardsMargin ? item.cardsMargin : 10 }}>
                  $ {item.price}
                </Typography>
              </CardContent>
            </Card>
          </div>
        )
      }
      // buy btn
      if(item.buyBtn) {
        return (
          <div className="slide" key={item.id}>
            <Card style={{ height: 370, width: 270, margin: '30px 10px'}} className='popular-items__card ${item.simpleOverlay'>
              <div className='popular-items__card-overlay'>
                <IconButton onClick={()=>this.addToCartList()}><img src="./components/popular_items/plus_btn.png" alt=""/></IconButton>
                <IconButton onClick={()=>this.addToWishes()}><img src="./components/popular_items/heart_btn.png" alt=""/></IconButton>
              </div>
              <Link to="/product" onClick={() => this.getCurrentProduct(item)}><CardMedia image={item.link} title="card" style={{ height: 270}}/></Link>
              <CardContent style={{ height: 70}}>
                <Typography variant="body2" color="textSecondary" component="p">
                  <Link to="/product" onClick={() => this.getCurrentProduct(item)} style={{color: 'rgba(0, 0, 0, 0.54)'}}>{item.text}</Link><br/>
                  <Link to="/product" onClick={() => this.getCurrentProduct(item)} style={{color: 'rgba(0, 0, 0, 0.54)'}}>{item.text1}</Link>
                </Typography>
                <div className="popular-items__buy-button-wr">
                  <Link to="/product" onClick={() => this.getCurrentProduct(item)}><p className="popular-items__buy-button-price">$ {item.price}</p></Link>
                  <button className="popular-items__buy-button" onClick={()=>this.addToCartList(item)}>BUY NOW</button>
                </div>
              </CardContent>
            </Card>
          </div>
        )
      }
      // message
      if(item.messageText) {
        return (
          <div className="slide" key={item.id}>
            <Card style={{ height: 370, width: 270, margin: '30px 10px'}} className='popular-items__card simple'>
              <div className='popular-items__card-overlay message'></div>
              <CardMedia image={item.link} title="card" style={{height: 370}}/>
              <CardContent style={{ height: 70}}>
                <Typography variant="body2" component="p" style={{ margin: '-160px 0 0 30px', color: 'white', textAlign: 'left', position: 'relative', zIndex: 1}}>
                  {item.messageText}<br/>
                  {item.messageText1}<br/>
                  {item.messageText2}
                </Typography>
                <Typography variant="body2"  component="p" style={{ margin: '10px 0 0 30px', color: 'white', textAlign: 'left', position: 'relative', zIndex: 1}}>
                  <img src={item.link2} alt="" style={{marginRight: 10}}/>
                  {item.messageText3}
                </Typography>
              </CardContent>
            </Card>
          </div>
        )
      }
      return (<div></div>)
    })
    // items JSON
    if(this.state.popularItems.length) {
      this.jsonItems = this.state.popularItems.map(jsonItem => 
        <Card key={jsonItem.id} style={{ height: 370, width: 270, margin: '30px 10px', borderRadius: '6px'}} className="popular-items__card">
          <CardMedia image={jsonItem.img} title="logo_small" style={{height: 270, backgroundSize: 'contain'}}/>
            <CardContent style={{ height: 70}}>
              <Typography variant="body2" color="textSecondary" component="p" style={{ marginBottom: '10px'}}>
                {jsonItem.content}
              </Typography>
              <Typography variant="body2" color="primary" component="p" style={{ marginTop: 10}}>
                {jsonItem.price}
              </Typography>
            </CardContent>
        </Card>
      )
    }
    return template.call(this);
  }
}

const mapStateToProps = (state) => ({wishQuantity: state.wishQuantity});
export default connect(mapStateToProps)(PopularItems);


