import React    from "react";
import template from "./Release.jsx";

class Release extends React.Component {
  constructor() {
    super();
    this.secondsSVG = React.createRef();
    this.state = {
      monthsVal: 0,
      daysVal: 0,
      hoursVal: 0,
      minutesVal: 0,
      secondsVal: 0,
      monthsStrokeDashoffset: 0,
      daysStrokeDashoffset: 0,
      hoursStrokeDashoffset: 0,
      minutesStrokeDashoffset: 0,
      secondsStrokeDashoffset: 0,
    }

  }
  componentDidMount() {
    this.countdown();
  }
  // countdown
  countdown() {
    setInterval(() => {
      const now = new Date().getTime();   
      const countDownDate = new Date("Dec 31, 2030 23:59").getTime();
      const distance = countDownDate - now;
      let months = "00"
      let days = "00"
      let hours = "00"
      let minutes = "00"
      let seconds = "00"
      if (distance > 0) {
        months = String(new Date(distance).getMonth()).padStart(2, "0");
        days = String((new Date(distance).getDate())-1).padStart(2, "0");
        hours = String((new Date(distance).getHours())-1).padStart(2, "0");
        minutes = String(new Date(distance).getMinutes()).padStart(2, "0");
        seconds = String(new Date(distance).getSeconds()).padStart(2, "0");
      }
      // percent
      const monthsPerc = Math.floor(100 * months / 31);
      const daysPerc = Math.floor(100 * days / 31);
      const hoursPerc = Math.floor(100 * hours / 24);
      const minutesPerc = Math.floor(100 * minutes / 60);
      const secondsPerc = Math.floor(100 * seconds / 60);
      // svg
      this.setState({monthsVal: months});
      this.setState({monthsStrokeDashoffset: monthsPerc*3.14});
      this.setState({daysVal: days});
      this.setState({daysStrokeDashoffset: daysPerc*3.14});
      this.setState({hoursVal: hours});
      this.setState({hoursStrokeDashoffset: hoursPerc*3.14});
      this.setState({minutesVal: minutes});
      this.setState({minutesStrokeDashoffset: minutesPerc*3.14});
      this.setState({secondsVal: seconds});
      this.setState({secondsStrokeDashoffset: secondsPerc*3.14});
    }, 1000);
  }
  render() {
    return template.call(this);
  }
}
export default Release;


